

.container
{
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 50px;
    padding-bottom: 30px;
    width: 500px;
    border: 5px solid rgb(219, 155, 15); 
    border-radius: 50px;
    height: 500px;
    box-sizing: border-box;
}
.header
{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
}
.text
{
    color: rgb(224, 219, 210);
    font-size: 32px;
    font-weight: 400;
}
.line
{
    width: 75px;
    height: 4px;
    background:orangered;
    border-radius: 9px;
}
.inputs
{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
     gap: 25px;
}
.inputs{
  display: flex;
  align-items: center;
  margin: auto;
  width: 200px;
  height: 80px;
  border-radius: 6px;
}
.input input
{
    height:50px;
    width: 100px;
    background: transparent;  
    outline: 2px solid white;
   font-size: 10px;
    border-radius: 50px;
}


.input input
{
    height:50px;
    width: 400px;
    background: transparent;
    border: none;
    font-size: 19px;
    border-radius: 50px;
}
.forget
{
    padding-left:62px;
    margin-top: 20px;
    color: #797979;
    font-size: 19px;
    margin-top: 210px;
}
.forget span
{
    color: blue;
    cursor: pointer;
    

}
.submit-container
{
    display: flex;
    
}
.submit
{
  display: flex;
  justify-content:center;
  align-items: center;
  width: 150px;
  height: 50px;
  color: white;
  background: rgb(219, 155, 15);
  border-radius: 50px;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
  border: none;
  box-shadow: 0,0,rgb(219, 155, 15);
  transition: 0.5s;
}

.submit:hover{
  box-shadow: 0 0 5px rgb(219, 155, 15),0 0 25px rgb(219, 155, 15),
  0 0 50px rgb(219, 155, 15), 0 0 100px rgb(219, 155, 15);
  opacity: 0.8;
}


